<template>

<div class="admin">
    <h1>Sida för generering av nya koder</h1>
    <p>Det genereras två länkar<br>
    Den första länken är till ett komplett test<br>
    Den andra länken är till resultatet från ovanstående länk.</p>
    <p>JUST NU LÄGGS BARA EN KOD I TAGET TILL. DEN FÖRSTA... DET FUNGERAR ENDAST LOKALT....</p>
        <v-form>
            <v-layout class="text-xs-right" style="padding-top:10px;">
                <v-flex xs6 md6 >    
                    <v-btn v-on:click="genCodes()">Generera 10 länkar</v-btn>
                </v-flex>
            </v-layout>
        </v-form>
        <br><br>
        <v-divider></v-divider>
        <br>
        <div v-for="code in codes" :key="code.code">
            <h3>https://larlid.github.io/?code={{ code }}</h3>
            <h3>https://larlid.github.io/?result={{ encryptedCode(code) }}</h3>
            <br>
        </div>
</div>
</template>

<script>

import axios from 'axios';
var hash = require('object-hash');
export default {
data() {
    return {
        codes: [
        ],
        salt: 'cfdmuxkidnjomuwyvayd',
    };
},
methods: {

    // Genereate codes for database
    genCodes() {
        let text = "";
        let possible = "abcdefghijklmnopqrstuvwxyz";
        for(let counter = 0; counter < 10; counter++) {
            for (let i = 0; i < 20; i++) {
                text += possible.charAt(Math.floor(Math.random() * possible.length));
            }
            this.codes.push(text);
            text = '';
        }
        //const res = await axios.post('http://localhost:4000/code', { code: this.username, question_number: index, question_answer: this.answer[index] } )
        axios.post('http://192.168.0.3:4000/code/', { code: this.codes[0] } ).then( res => { 
            if(res.data.message == this.codes[0]) {
                console.log("Number: " + "0" + " ok.")

            }
        })

    },
    encryptedCode(a_code) {
        let myString = hash(a_code + this.salt, { algorithm: 'sha1', encoding: 'base64' })
        myString = myString.replace(/[`~!@#$%^&*()_|+\-=?;:'",.<>\{\}\[\]\\\/]/gi, '');
        console.log(myString)
        return myString;
    },



},

}
</script>

<style scoped>
   
    .admin {
        text-align: left;
        padding: 10%;
    }
</style>


